import React, { useState, useEffect } from 'react'
import { useSwipeable } from 'react-swipeable'
import styled from 'styled-components'
import MovieCard from './MovieCard'
import api from './Api'

import { ReactComponent as AcceptButton } from './assets/accept.svg'
import { ReactComponent as RejectButton } from './assets/reject.svg'

const ButtonsBox = styled.div``

const MainButton = styled.button`
  width: 100px;
  height: 100px;
  border-radius: 90%;
  border: none;
  cursor: pointer;
  margin: 20px 40px;
  outline: none;
  background-color: white;
  box-sizing: border-box;
  box-shadow: 10px 7px 65px -13px rgba(0, 0, 0, 0.75);
`

interface items {
  movies: Array<object>
}

function MoviesCards(props: items) {
  const { movies } = props
  const [count, setCount] = useState(0)
  const [currentItem, setCurrentItem] = useState(null)

  useEffect(() => {
    setCurrentItem(movies[count])
  }, [movies, count])

  const onAcceptHandler = (id: string) => {
    api.acceptMovie(id)
    setCount(count + 1)
  }
  const onRejectHandler = (id: string) => {
    api.rejectMovie(id)
    setCount(count + 1)
  }
  const handler = useSwipeable({
    onSwipedLeft: () => {
      onRejectHandler(currentItem.id)
    },
    onSwipedRight: () => {
      onRejectHandler(currentItem.id)
    }
  })
  if (currentItem === null) {
    return <div>waiter</div>
  } else if (count === movies.length) {
    return <div>there is no more items</div>
  } else {
    return (
      <>
        <div {...handler}>
          <MovieCard item={currentItem}></MovieCard>
        </div>
        <ButtonsBox>
          <MainButton onClick={() => onRejectHandler(currentItem.id)}>
            <RejectButton />
          </MainButton>
          <MainButton onClick={() => onAcceptHandler(currentItem.id)}>
            <AcceptButton />
          </MainButton>
        </ButtonsBox>
      </>
    )
  }
}

export default MoviesCards
